<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者：{{blog.author}}</p>
    <p>分类：</p>
    <ul>
      <li v-for="(category,index) in blog.categories" v-bind:key="'category'+index">{{category}}</li>
    </ul>
    <button @click="deleteSingleBlog()">删除</button>
    <router-link :to="'/edit/' + id">编辑</router-link>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    };
  },
  created() {
    // this.$http
    //   .get("https://9eCw4HXv.api.lncld.net/classes/vueblog/" + this.id)
    //   .then(function(data) {
    //     // console.log(data);
    //     // this.blog=data.content;
    //     return data.json();
    //   })
    //   .then(function(data) {
    //     this.blog = data;
    //   });
    var self = this;
    var query = new AV.Query("vueblog");
    query
      .get(self.id)
      .then(function(todo) {
        // todo 就是 objectId 为 582570f38ac247004f39c24b 的 Todo 实例
        self.blog = todo.attributes;
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  methods: {
    deleteSingleBlog() {
      //   this.$http
      //     .delete("https://9eCw4HXv.api.lncld.net/1.1/classes/vueblog/" + this.id)
      //     .then(response => {
      //       this.$router.push({ path: "/" });
      //     });
      var todo = AV.Object.createWithoutData("vueblog", this.id);
      todo.destroy().then(data=>{
          this.$router.push({ path: "/" });
      });
    }
  }
};
</script>

<style scoped>
#single-blog {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
}
</style>





