<template>
  <div id="edit-blog">
    <h2>编辑博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories" />
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories" />
        <label>Rect.js</label>
        <input type="checkbox" value="Rect.js" v-model="blog.categories" />
        <label>Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories" />
      </div>
      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="(author,index) in authors" v-bind:key="'author'+index">{{author}}</option>
      </select>
      <button @click.prevent="post">编辑博客</button>
    </form>
    <dir v-if="submmited">
      <h3>您的博客发布成功！</h3>
    </dir>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="(category,index) in blog.categories" v-bind:key="'category'+index">{{category}}</li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  // http://jsonplaceholder.typicode.com/
  name: "add-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {},
      authors: ["Hemiah", "Henry", "Bucky"],
      submmited: false
    };
  },
  methods: {
    post: function() {
      //   this.$http
      //     .put("https://9eCw4HXv.api.lncld.net/1.1/classes/vueblog/" + this.id, this.blog)
      //     .then(function(data) {
      //       console.log(data);
      //       this.submmited = true;
      //     });
      var self=this;
      var todo = AV.Object.createWithoutData("vueblog", this.id);
      todo.set("title", this.blog.title);
      todo.set("content", this.blog.content);
      todo.set("categories", this.blog.categories);
      todo.set("author", this.blog.author);
      todo.save().then(function(data){
          console.log(data);
          self.submmited = true;
      }).catch(function(error){
          console.log(error);
      });
    },
    fetchData() {
      //   this.$http
      //     .get("https://9eCw4HXv.api.lncld.net/1.1/classes/vueblog/" + this.id)
      //     .then(response => {
      //       this.blog = response.body;
      //     });
      var self=this;
      var query = new AV.Query("vueblog");
      query.get(this.id).then(function(todo) {
        // todo 就是 objectId 为 582570f38ac247004f39c24b 的 Todo 实例
        self.blog = todo.attributes;
      });
    }
  },
  created() {
    this.fetchData();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog * {
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 2px;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}

#checkboxes label {
  display: inline-block;
  margin-top: 0;
}

#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: white;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3 {
  margin-top: 10px;
}
</style>



