<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories" />
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories" />
        <label>Rect.js</label>
        <input type="checkbox" value="Rect.js" v-model="blog.categories" />
        <label>Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories" />
      </div>
      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="(author,index) in authors" v-bind:key="'author'+index">{{author}}</option>
      </select>
      <button @click.prevent="post">添加博客</button>
    </form>
    <dir v-if="submmited">
      <h3>您的博客发布成功！</h3>
    </dir>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="(category,index) in blog.categories" v-bind:key="'category'+index">{{category}}</li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  // http://jsonplaceholder.typicode.com/
  name: "add-blog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: ""
      },
      authors: ["Hemiah", "Henry", "Bucky"],
      submmited: false
    };
  },
  methods: {
    post: function() {
      // this.$http
      //   .post("https://9eCw4HXv.api.lncld.net/1.1/classes/vueblog",this.blog)
      //   .then(function(data) {
      //     console.log(data);
      //     this.submmited = true;
      //   });
      // 声明 class
      var self=this;
      var vueblog = AV.Object.extend("vueblog");
      // 构建对象
      var vueblog = new vueblog();
      // 为属性赋值
      vueblog.set("title", this.blog.title);
      vueblog.set("content", this.blog.content);
      vueblog.set("categories", this.blog.categories);
      vueblog.set("author", this.blog.author);
      // 将对象保存到云端
      vueblog.save().then(
        function(vueblog) {
          // 成功保存之后，执行其他逻辑
          self.submmited = true;
        },
        function(error) {
          // 异常处理
        }
      );
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog * {
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 2px;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}

#checkboxes label {
  display: inline-block;
  margin-top: 0;
}

#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: white;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3 {
  margin-top: 10px;
}
</style>



